<template>
  <div class="i3v-add-attr-panel">
    <!--  start -->
    <el-dialog
      append-to-body
      class="ss-dialog no-header-bottom"
      title=""
      width="460px"
      :visible.sync="dialogVisible"
      @close="handleClose"
    >
      <div class="fileattr-dialog-content">
        <el-form
          label-width="100px"
          ref="customAttribute"
          :model="customAttribute"
          :rules="rules"
        >
          <el-form-item prop="dictId" v-show="false"></el-form-item>
          <el-form-item label="" prop="dictName">
            <el-input
              v-model.trim="customAttribute.dictName"
              placeholder=""
              :maxlength="10"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="dictItemType">
            <el-select
              v-model="customAttribute.dictItemType"
              placeholder=""
              style="width: 100%"
              :disabled="!!customAttribute.dictId"
              @change="changeValue"
            >
              <el-option label="" :value="1"></el-option>
              <el-option label="" :value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="itemText">
            <div class="attr-value">
              <el-input
                v-model.trim="customAttribute.itemText"
                :placeholder="
                  customAttribute.dictItemType
                    ? `${
                        customAttribute.dictItemType === 1
                          ? ''
                          : ''
                      }`
                    : ''
                "
                :disabled="customAttribute.dictItemType !== 1"
                :maxlength="10"
              ></el-input>
              <i
                v-if="customAttribute.dictItemType === 2"
                class="el-icon-more pointer"
                style="margin-left: 16px"
                @click="showInnerDialog"
              ></i>
            </div>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer">
        <el-button type="info" @click="dialogVisible = false"
          >&nbsp;&nbsp;</el-button
        >
        <el-button style="width: 110px" type="primary" @click="confirmAddAttr"
          >&nbsp;&nbsp;</el-button
        >
      </div>
    </el-dialog>
    <!--  end -->
    <!--  start -->
    <el-dialog
      append-to-body
      class="ss-dialog"
      title=""
      width="460px"
      :visible.sync="innerVisible"
    >
      <div class="inner-dialog-content">
        <div class="attr-list">
          <div class="attr-list-row">
            <div class="attr-list-item border-right"></div>
            <div class="attr-list-item"></div>
          </div>
          <div
            v-for="item in attributeFilterList"
            class="attr-list-row pointer"
            :key="item.id"
            :class="{
              'is-active': currentAttr && currentAttr.id === item.id,
            }"
            @click="selectAttr(item)"
          >
            <div class="attr-list-item">
              <span>{{ item.itemText }}</span>
            </div>
            <div class="attr-list-item">
              <span>{{ item.description }}</span>
            </div>
          </div>
        </div>
        <div class="attr-button">
          <el-button type="primary" @click="confirmAttrList"></el-button>
          <el-button type="info" @click="innerVisible = false"></el-button>
          <el-button type="primary" @click="updateAnAttr(null)"></el-button>
          <el-button
            type="primary"
            :disabled="currentAttr === null"
            @click="updateAnAttr(currentAttr)"
            ></el-button
          >
          <el-button
            type="primary"
            :disabled="currentAttr === null"
            @click="deleteAnAttr"
            ></el-button
          >
        </div>
      </div>
    </el-dialog>
    <!--  end -->
    <!--  start -->
    <el-dialog
      append-to-body
      class="ss-dialog no-header-bottom"
      title=""
      width="400px"
      :visible.sync="attrVisible"
      @close="handleAnAttrClose"
    >
      <div style="padding: 0 30px">
        <el-form
          label-width="80px"
          ref="anAttr"
          :model="anAttr"
          :rules="anAttrRules"
        >
          <el-form-item label="" prop="itemText">
            <el-input
              v-model.trim="anAttr.itemText"
              placeholder=""
              :maxlength="10"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="description">
            <el-input
              v-model.trim="anAttr.description"
              placeholder=""
              :maxlength="10"
            ></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer">
        <el-button type="info" @click="attrVisible = false"
          >&nbsp;&nbsp;</el-button
        >
        <el-button
          style="width: 110px"
          type="primary"
          @click="confirmUpdateAttr"
          >&nbsp;&nbsp;</el-button
        >
      </div>
    </el-dialog>
    <!--  end -->
  </div>
</template>
<script>
import { cloneDeep, pick } from "lodash";

export default {
  name: "I3vAddAttrPanel",
  data() {
    return {
      dialogVisible: false,
      innerVisible: false,
      customAttribute: {
        dictId: "",
        dataId: "", // id
        delFlag: 0,
        dictName: "", // 
        dictItemType: "", // 12
        itemText: "", // 
        description: "",
        dictType: 2, // 12
        detailDTOS: [], // 
      }, // 
      rules: {
        dictName: {
          required: true,
          message: "",
          trigger: ["change", "blur"],
        },
        dictItemType: {
          required: true,
          message: "",
          trigger: ["change", "blur"],
        },
      },
      attributeList: [], // 
      currentAttr: null, // 
      attrVisible: false,
      anAttr: {
        delFlag: 0, // (0.1.)
        id: "",
        isSelected: 0, // (1:0)
        itemText: "",
        description: "",
      },
      anAttrRules: {
        itemText: {
          required: true,
          message: "",
          trigger: ["change", "blur"],
        },
      },
    };
  },
  computed: {
    // 
    attributeFilterList() {
      return this.attributeList.filter((item) => item.delFlag !== 1);
    },
  },
  methods: {
    editFormDefData(value) {
      console.log('value :>> ', value);
      // let obj = pick(value, ["id", "dictId", "dictName", "dictItemType"]);
      // obj.dataId = '';
      // obj.itemText =
      //   value.dictItemType === 1 ? value.detailDTOList[0].itemText : "";
      // obj.description = "";
      // obj.detailDTOS = cloneDeep(value.detailDTOList);
      // this.dialogVisible = true;
      // this.$nextTick(() => {
      //   this.customAttribute = obj;
      // })
      //
      let obj = {};
      if (value) {
        obj = pick(value, ["id", "dictId", "dictName", "dictItemType"]);
        obj.dataId = "";
        obj.itemText =
          value.dictItemType === 1 ? value.detailDTOList[0].itemText : "";
        obj.description = "";
        obj.detailDTOS = cloneDeep(value.detailDTOList);
        // this.customAttribute = obj;
      } else {
        obj = {
          dataId: "",
          delFlag: 0,
          dictType: 2, // 1, 2
          dictId: "",
          dictName: "",
          dictItemType: "",
          itemText: "",
          description: "",
          detailDTOS: [],
        };
      }
      this.dialogVisible = true;
      this.$nextTick(() => {
        this.customAttribute = obj;
        console.log('this.customAttribute :>> ', this.customAttribute);
      });
    },
    showDialog() {
      this.dialogVisible = true;
    },
    handleClose() {
      this.detailDTOS = [];
      this.$refs.customAttribute.resetFields();
    },
    /**
     * @description: 
     * @param {Object} value 
     * @return {void}
     */
    selectAttr(value) {
      if (this.currentAttr && this.currentAttr.id === value.id) {
        this.currentAttr = null;
      } else {
        this.currentAttr = value;
      }
    },
    /**
     * @description: 
     * @param {String} value 
     * @return {void}
     */
    changeValue() {
      this.customAttribute.itemText = "";
      this.customAttribute.description = "";
      this.customAttribute.detailDTOS = [];
      this.$nextTick(() => {
        this.$refs.customAttribute.clearValidate("dictItemType");
      });
    },
    /**
     * @description: 
     * @return {void}
     */
    confirmUpdateAttr() {
      this.$refs.anAttr.validate(async (valid) => {
        if (!valid) {
          return;
        }
        const param = cloneDeep(this.anAttr);
        if (param.id) {
          const index = this.attributeList.findIndex(
            (item) => item.id === param.id
          );
          this.$set(this.attributeList, index, param);
          this.currentAttr = { ...param };
        } else {
          param.id = `attr-${Date.now()}`;
          this.attributeList.push(param);
        }
        this.attrVisible = false;
      });
    },
    /**
     * @description:  anAttr 
     * @return {void}
     */
    handleAnAttrClose() {
      this.$refs.anAttr.resetFields();
    },
    /**
     * @description:   
     * @return {void}
     */
    updateAnAttr(value) {
      if (value) {
        this.anAttr = { ...value };
      } else {
        this.anAttr = {
          id: "",
          isSelected: 0,
          itemText: "",
          description: "",
          delFlag: 0,
        };
      }
      this.attrVisible = true;
    },
    /**
     * @description: 
     * @return {void}
     */
    async confirmAttrList() {
      this.customAttribute.detailDTOS = this.attributeList;
      this.innerVisible = false;
    },
    /**
     * @description: 
     * @return {void}
     */
    deleteAnAttr() {
      if (this.currentAttr.isSelected) {
        return this.$message.warning("");
      }
      const index = this.attributeList.findIndex(
        (item) => item.id === this.currentAttr.id
      );
      if (this.currentAttr.id.startsWith("attr-")) {
        this.attributeList.splice(index, 1);
      } else {
        this.$set(this.attributeList[index], "delFlag", 1);
      }
      this.currentAttr = null;
    },
    /**
     * @description:  
     * @return {void}
     */
    confirmAddAttr() {
      this.$refs.customAttribute.validate(async (valid) => {
        if (!valid) {
          return;
        }
        if (
          this.customAttribute.dictItemType === 2 &&
          this.customAttribute.detailDTOS.length === 0
        ) {
          return this.$message.warning("");
        }
        if (!this.customAttribute.dictId) {
          // 
          let payload = {
            dataId: this.customAttribute.dataId,
            delFlag: 0,
            dictType: 2,
            dictName: this.customAttribute.dictName,
            dictItemType: this.customAttribute.dictItemType,
          };
          if (this.customAttribute.dictItemType === 1) {
            payload.detailDTOS = [
              {
                delFlag: 0,
                id: "",
                isSelected: 1,
                itemText: this.customAttribute.itemText,
                description: "",
              },
            ];
          } else {
            payload.detailDTOS = cloneDeep(this.customAttribute.detailDTOS);
            payload.detailDTOS.forEach((element) => {
              element.id.startsWith("attr-") && (element.id = "");
            });
          }
          this.$emit("add-attribute", payload);
        } else {
          // 
          let payload;
          if (this.customAttribute.dictItemType === 1) {
            payload = pick(this.customAttribute, [
              "dataId",
              "dictId",
              "dictName",
              "dictItemType",
            ]);
            payload.detailDTOS = cloneDeep(this.customAttribute.detailDTOS);
            payload.detailDTOS[0].itemText = this.customAttribute.itemText;
          } else {
            payload = pick(this.customAttribute, [
              "dataId",
              "dictId",
              "dictName",
              "dictItemType",
            ]);
            payload.detailDTOS = cloneDeep(this.customAttribute.detailDTOS);
            payload.detailDTOS.forEach((element) => {
              element.id.startsWith("attr-") && (element.id = "");
            });
          }
          this.$emit("edit-attribute", payload);
        }
        this.dialogVisible = false;
      });
    },
    /**
     * @description:   
     * @return {void}
     */
    showInnerDialog() {
      this.attributeList = cloneDeep(this.customAttribute.detailDTOS);
      this.currentAttr = null;
      this.innerVisible = true;
    },
  },
};
</script>
<style lang="scss">
.fileattr-dialog-content {
  padding: 0 30px;

  .attr-value {
    display: flex;
    align-items: center;
  }
}
</style>
<style lang="scss">
.ss-dialog .el-dialog {
  border-radius: 10px;
}

.ss-dialog .el-dialog .el-dialog__header {
  box-sizing: content-box;
  height: 24px;
  padding: 28px 0 21px;
  margin: 0 30px 0 30px;
  border-bottom: 1px solid #e6ebf2;
}

.ss-dialog.no-header-bottom .el-dialog .el-dialog__header {
  border-bottom-color: #fff;
}

.ss-dialog.no-header .el-dialog .el-dialog__header {
  display: none;
}

.ss-dialog .el-dialog .el-dialog__header .el-dialog__title {
  line-height: 24px;
  font-size: 18px;
  font-weight: 500;
  color: #344563;
}

.ss-dialog .el-dialog .el-dialog__header .el-dialog__headerbtn {
  top: 30px;
  right: 30px;
  font-size: 20px;
}

.ss-dialog .el-dialog .el-dialog__header .el-dialog__headerbtn i {
  display: block;
}

.ss-dialog .el-dialog .el-dialog__body {
  padding: 0;
}

.ss-dialog .el-dialog .el-dialog__footer {
  min-height: 90px;
  padding: 26px 30px 24px;
  background-color: #f5f7fa;
  border-radius: 0 0 10px 10px;
}
</style>
