<template>
  <div class="i3v-file-attribute__wrapper">
    <div class="i3v-file-attribute-file-disk">
      <div class="right-close">
        <!-- <svg-icon icon="close"></svg-icon> -->
      </div>
      <el-tabs v-model="activeTab">
        <el-tab-pane label="" name="attribute">
          <I3vAttrPanel
            ref="I3vAttrPanel"
            @add-attribute="handleAddAttribute"
            @edit-attribute="handleEditAttribute"
            @delete-attribute="handleDelAttribute"
            @update-dictItem="handleUpdateAttribute"
            @change-category="handleChangeCategory"
          ></I3vAttrPanel>
        </el-tab-pane>
        <el-tab-pane label="" name="rela">
          <el-table
            header-cell-class-name="app-header-cell"
            :data="associateList"
          >
            <el-table-column
              label=""
              prop="eventName"
              min-width="180"
            ></el-table-column>
            <el-table-column
              label=""
              prop="eventType"
              min-width="180"
            ></el-table-column>
            <el-table-column
              label=""
              prop="responsibleBy"
              min-width="180"
            ></el-table-column>
            <el-table-column
              label=""
              prop="status"
              min-width="180"
            ></el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="" name="log">
          <div
            v-for="(item, index) in operationLog"
            class="log-item"
            :key="index"
          >
            <span class="log-item__value">{{ item.optContent }}</span>
            <div class="log-item__user">
              <el-avatar icon="el-icon-user-solid" :size="24"></el-avatar>
              <span style="padding-left: 12px">{{
                item.createBy.realName || item.createBy.userName
              }}</span>
            </div>
            <div class="log-item__date">{{ item.createTime }}</div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>
<script>
import I3vAttrPanel from "./attr-panel.vue";

export default {
  name: "I3vFileAttribute",
  components: { I3vAttrPanel },
  data() {
    return {
      activeTab: "attribute",
      associateList: [],
      detailDTOList: [],
      attributeDetail: {
        fsDictQueryVOList: [],
        filePosition: "",
        folderPosition: "",
        fileSize: "",
        folderSize: "",
        updateTime: "",
        updateBy: "",
        createTime: "",
        createBy: "",
      },
      // /
      currentItem: {
        createTime: "",
      },
      operationLog: [],
      isPreview: false,
      dialogVisible: false,
      innerVisible: false,
      customAttribute: {
        dictId: "",
        dataId: "", // id
        delFlag: 0,
        dictName: "", // 
        dictItemType: "", // 12
        itemText: "", // 
        description: "",
        dictType: 2, // 12
        detailDTOS: [], // 
      }, // 
      rules: {
        dictName: {
          required: true,
          message: "",
          trigger: ["change", "blur"],
        },
        dictItemType: {
          required: true,
          message: "",
          trigger: ["change", "blur"],
        },
      },
      attributeList: [], // 
      currentAttr: null, // 
      attrVisible: false,
      anAttr: {
        delFlag: 0, // (0.1.)
        id: "",
        isSelected: 0, // (1:0)
        itemText: "",
        description: "",
      },
      anAttrRules: {
        itemText: {
          required: true,
          message: "",
          trigger: ["change", "blur"],
        },
      },
      // ,,
      // ()1, 2,
      defaultSelect: [],
    };
  },
  computed: {
    // 
    attributeFilterList() {
      return this.attributeList.filter((item) => item.delFlag !== 1);
    },
  },
  methods: {
    handleAddAttribute(value) {
      this.$emit("add-attribute", value);
    },
    handleEditAttribute(value) {
      this.$emit("edit-attribute", value);
    },
    handleDelAttribute(value) {
      this.$emit("delete-attribute", value);
    },
    handleUpdateAttribute(value) {
      this.$emit("update-dictItem", value);
    },
    handleChangeCategory(value) {
      this.$emit("change-category", value);
    },
    /**
     * @description: 
     * @return {void}
     */
    handleClear() {
      this.setAttributeDetail();
      this.setLog();
      this.setAssociateList();
    },
    /**
     * @description: ref associateList 
     * @param {Array} value 
     * @return {void}
     */
    setAssociateList(value) {
      if (value && value instanceof Array && value.length > 0) {
        this.associateList = value;
      } else {
        this.associateList = [];
      }
    },
    /**
     * @description: ref operationLog 
     * @param {Array} value 
     * @return {void}
     */
    setLog(value) {
      if (value && value instanceof Array && value.length > 0) {
        this.operationLog = value;
      } else {
        this.operationLog = [];
      }
    },
    /**
     * @description: ref attributeDetail  
     * @return {void}
     */
    setAttributeDetail(value) {
      this.$refs.I3vAttrPanel.setAttributeDetail(value)
    },
    /**
     * @description: ref attributeDetail  
     * @return {void}
     */
    setCategoryTree(value) {
      this.$refs.I3vAttrPanel.setCategoryTree(value)
    },
  },
};
</script>
<style lang="scss">
.i3v-file-attribute__wrapper {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  display: flex;
  overflow-x: auto;

  ::v-deep .el-button {
    border-radius: 5px;
  }

  .i3v-file-attribute-file-disk {
    box-sizing: border-box;
    flex: 1;
    min-width: 400px;
    border-radius: 5px 5px 0 0;
    background-color: #fff;
    position: relative;

    .right-close {
      position: absolute;
      z-index: 1;
      top: 30px;
      right: 30px;
      width: 20px;
      height: 20px;
      cursor: pointer;

      &:hover {
        .svg-icon {
          color: #1664ff !important;
        }
      }
    }

    ::v-deep .el-tabs {
      height: 100%;
      display: flex;
      flex-direction: column;

      .el-tabs__content {
        flex: 1;

        .el-tab-pane {
          height: 100%;
          overflow: auto;
        }
      }
    }

    .attribute-wrapper {
      height: 70%;
      overflow: auto;

      .attr-item {
        font-size: 14px;
        display: flex;
        border-top: 1px solid #f7f7f7;
        border-bottom: 1px solid #f7f7f7;

        .attr-item-label {
          flex-shrink: 0;
          box-sizing: border-box;
          width: 100px;
          min-height: 32px;
          padding: 6px 8px;
          text-align: center;
          background-color: #f2f2f2;

          > span {
            line-height: 20px;
            word-break: break-all;
          }
        }

        .attr-item-label-value {
          box-sizing: border-box;
          width: calc(100% - 100px);
          min-height: 32px;
          padding: 0 6px;
          background-color: #fff;
          display: flex;
          align-items: center;
          position: relative;

          &.custom-attribute {
            padding-right: 34px;
          }

          > span {
            word-break: break-all;
          }

          &:hover {
            .attr-action {
              display: block;
            }
          }

          .descr {
            padding-left: 6px;
            color: #c0c4cc;
          }

          .attr-action {
            display: none;
            position: absolute;
            top: calc(50% - 8px);
            right: 6px;
            font-size: 16px;
            cursor: pointer;

            &.attr-edit {
              right: 28px;
            }
          }
        }
      }
    }

    .associate-wrapper {
      height: 30%;
      overflow: auto;
    }

    .log-item {
      display: flex;
      align-items: center;
      font-size: 16px;
      font-weight: 500;
      color: #ababab;
      padding-right: 16px;
      padding-bottom: 28px;
      margin-bottom: 12px;
      position: relative;

      .log-item__value {
        flex: 1;
        font-size: 14px;
        color: #666;
        word-break: break-all;
      }

      .log-item__user {
        display: flex;
        align-items: center;
        padding-left: 12px;
      }

      .log-item__date {
        position: absolute;
        right: 16px;
        bottom: 0;
      }
    }
  }
}
.fileattr-dialog-content {
  padding: 0 30px;

  .attr-value {
    display: flex;
    align-items: center;
  }
}

.inner-dialog-content {
  padding: 20px 30px;
  display: flex;

  .attr-list {
    box-sizing: border-box;
    width: 240px;
    height: 240px;
    padding: 8px;
    border: 1px solid #f7f7f7;
    overflow-y: auto;

    .attr-list-row {
      display: flex;
      border-radius: 5px;
      font-size: 14px;
      font-weight: 500;
      color: #344563;

      &.is-active {
        color: #fff;
        background-color: #1664ff;
      }

      .attr-list-item {
        box-sizing: border-box;
        width: 50%;
        padding: 6px 8px;

        > span {
          line-height: 20px;
          word-break: break-all;
        }

        &.border-right {
          border-right: 1px solid #f7f7f7;
        }
      }
    }
  }

  .attr-button {
    margin-left: 24px;
    display: flex;
    flex-direction: column;

    .el-button {
      margin-bottom: 8px;
    }

    .el-button + .el-button {
      margin-left: 0;
    }
  }
}
</style>

<style lang="scss">
@mixin popover($min-width: 80px) {
  box-sizing: border-box;
  min-width: $min-width !important;
  padding: 10px 8px !important;
  border-radius: 5px;
  border: 1px solid #d3dae6;
  box-shadow: 0px 0px 5px 2px rgba(94, 108, 132, 0.2);
}

@mixin popover-item {
  height: 32px;
  line-height: 32px;
  padding: 0 8px;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 500;
  color: #344563;
  text-align: center;

  &.disabled {
    color: #abb4c4;
    cursor: not-allowed;
  }

  &:not(.del):not(.disabled):hover {
    color: #fff;
    background-color: #1664ff;
  }

  &.del:hover {
    color: #ff5733;
  }
}
.category-popover {
  @include popover(96px);
  width: fit-content !important;
}

.category-popover-item {
  @include popover-item;
  box-sizing: border-box;
  text-align: left;
  padding: 0 12px;
  font-size: 14px;
  cursor: pointer;
}
</style>
<style lang="scss">
.ss-dialog .el-dialog {
  border-radius: 10px;
}

.ss-dialog .el-dialog .el-dialog__header {
  box-sizing: content-box;
  height: 24px;
  padding: 28px 0 21px;
  margin: 0 30px 0 30px;
  border-bottom: 1px solid #e6ebf2;
}

.ss-dialog.no-header-bottom .el-dialog .el-dialog__header {
  border-bottom-color: #fff;
}

.ss-dialog.no-header .el-dialog .el-dialog__header {
  display: none;
}

.ss-dialog .el-dialog .el-dialog__header .el-dialog__title {
  line-height: 24px;
  font-size: 18px;
  font-weight: 500;
  color: #344563;
}

.ss-dialog .el-dialog .el-dialog__header .el-dialog__headerbtn {
  top: 30px;
  right: 30px;
  font-size: 20px;
}

.ss-dialog .el-dialog .el-dialog__header .el-dialog__headerbtn i {
  display: block;
}

.ss-dialog .el-dialog .el-dialog__body {
  padding: 0;
}

.ss-dialog .el-dialog .el-dialog__footer {
  min-height: 90px;
  padding: 26px 30px 24px;
  background-color: #f5f7fa;
  border-radius: 0 0 10px 10px;
}
</style>